import React,{Component} from 'react';
import {Redirect} from 'react-router-dom';
import axios from 'axios';
import PropTypes from 'prop-types';
import {reactLocalStorage} from 'reactjs-localstorage';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import Switch from '@material-ui/core/Switch';
 
const styles = theme => ({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing.unit,
      minWidth: 150,
    },
    selectEmpty: {
      marginTop: theme.spacing.unit * 0,
    },
    group: {
        margin: `${theme.spacing.unit}px 0`,
        marginTop: '0px'
      },
  });
class BrandEdit extends Component{
    constructor(props){
        super(props);
        let brdid = this.props.match.params.id;
        this.state = {
            name : "",
            brandid : brdid,
            showOnWeb : "Y",
            checked : true
        }
        
        this.brandHandle = this.brandHandle.bind(this);
        this.brandHandle();
    }

    brandHandle = () => {
        let id = this.props.match.params.id;
        let token= reactLocalStorage.get('auth');
        let AuthStr = "Bearer ".concat(token);
        axios.get('http://13.229.249.101/dashboard/api/brand/view/'+id, { headers: { Authorization: AuthStr } })
        .then(response => {
            const datas = response.data;
            if(datas.showOnWeb === 'Y'){
                var aktif = true;
            }
            else{
                var aktif = false;
            }
            this.setState({
                name : datas.name,
                showOnWeb : datas.showOnWeb,
                checked : aktif
            })
        })
        .catch((error) => {
            console.log('error ' + error);
        });
    }
    
    handleChange = event => {
        this.setState({ [event.target.name]: event.target.value });
    };

    handleChecked = name => event => {
        this.setState({ [name]: event.target.checked });
    };

    submitHandler = (event) => {
        if(this.state.checked === true){
            var show = 'Y';
        }
        else{
            var show = 'N';
        }
        const payload = {
            "id" : this.state.brandid,
            "name" : this.state.name,
            "showOnWeb" : show
        }
        let token= reactLocalStorage.get('auth');
        const AuthStr = "Bearer ".concat(token); 
        axios.put('http://13.229.249.101/dashboard/api/brand/edit', payload, { headers: { Authorization: AuthStr } })
        .then((response) => {
            this.brandHandle();
        })
        .catch((error) => {
            console.log(error);
        });  
        event.preventDefault();     
    }

    componentDidUpdate(prevProps){
       if(prevProps.match.params.id !== this.props.match.params.id){
            this.brandHandle(); 
       }
    }


    render(){
        let page = "brand_edit";
        let role = reactLocalStorage.get('role');
        let edit = role.includes(page);

        return(
            edit === false ?<Redirect to="/admin" /> :
            <article className="article">
                <div className="box-default m-3">
                <div className="box-body py-5">
                    <div className="form-group">
                        <h6>Edit Brand</h6>
                    </div>
                    <form  onSubmit={this.submitHandler}>

                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Brand Name</label>
                        <div className="col-md-10">
                        <Input
                            inputProps={{name: 'name'}}
                            onChange={this.handleChange}
                            value={this.state.name}
                            placeholder="Brand Name"
                            id="inputName2"
                            fullWidth
                        />
                        </div>
                    </div>

                    <div className="form-group row">
                        <label htmlFor="inputName2" className="col-md-2 col-form-label">Show On Web</label>
                        <div className="col-md-10">        
                            <Switch
                            checked={this.state.checked}
                            onChange={this.handleChecked('checked')}
                            value={this.state.showOnWeb}
                            color="primary"
                            />
                        </div>
                    </div>
                    
                    <div className="form-group row">
                        <div className="col-md-2"></div>
                        <div className="col-md-10">
                        <Button variant="contained" className="btn-w-md" type="submit"> Submit </Button>
                        </div>
                    </div>
                    </form>

                </div>
                </div>

            </article>
        )
    }
}
BrandEdit.propTypes = {
    classes: PropTypes.object.isRequired,
  };
export default  withStyles(styles)(BrandEdit);